body{margin:0;background-color:#323841}header{display:flex;justify-content:space-around;align-items:center}header h1{font-size:3rem;color:#fe6869;font-family:Rubik,sans-serif}header .nav{display:flex;justify-content:space-evenly;align-items:center;color:#ede7e3;max-width:450px;width:100%}header .nav a{color:inherit;text-decoration:none;font-size:1.3rem;transition:all .3s ease;font-family:Karla,sans-serif}header .nav a:active,header .nav a:focus,header .nav a:hover{cursor:pointer;text-decoration:underline;color:#fe6869}.root{margin:auto;background-color:#0173d4;display:grid;grid-template-columns:repeat(7,1fr);max-width:400px;width:100%;grid-gap:5px}main{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}main #para{color:#fe6869}.div{width:100%;background-color:#323841;border-radius:500%;height:60px}